import cookies from "browser-cookies";
const settings = {
    prefix: 'velan'
};
class Storage {
  set(key, value) {
      cookies.set(`${settings.prefix}-${key}`, value)
  }

  get(key) {
      return cookies.get(`${settings.prefix}-${key}`)
  }

  remove(key) {
      cookies.erase(`${settings.prefix}-${key}`)
  }
}


const storage = new Storage();

export { storage };
