import React, { Suspense, lazy } from 'react';
import { Switch,BrowserRouter as Router, Route, Redirect } from 'react-router-dom';
import ProtectedRoute from "./components/ProtectedRoute";
import Spinner from '../app/shared/Spinner';

const Dashboard = lazy(() => import('./pages/Dashboard'));

const Visits = lazy(() => import('./pages/Visits'));
const VisitDetail = lazy(() => import('./pages/VisitDetail'));

const Users = lazy(() => import('./pages/Users'));
const UserForm = lazy(() => import('./pages/UserForm'));

const Login = lazy(() => import('./pages/Login'));



function AppRoutes () {
    return (
      <Suspense fallback={<Spinner/>}>
        <Switch>
          {/* <Router basename={`${process.env.PUBLIC_URL}`}> */}
            <ProtectedRoute exact path={`${process.env.PUBLIC_URL}/dashboard`} component={ Dashboard } />

            <ProtectedRoute path={`${process.env.PUBLIC_URL}/pages/visits`} component={ Visits } />
            <ProtectedRoute path={`${process.env.PUBLIC_URL}/pages/visit`} component={ VisitDetail } />

            <ProtectedRoute path={`${process.env.PUBLIC_URL}/pages/users`} component={ Users } />
            <ProtectedRoute path={`${process.env.PUBLIC_URL}/pages/userform`} component={ UserForm } />

            <Route path={`${process.env.PUBLIC_URL}/login`} component={ Login } />

            <Redirect to={`${process.env.PUBLIC_URL}/login`} />
            
          {/* </Router>       */}
        </Switch>
      </Suspense>
    );
}

export default AppRoutes;